<template>
  <div>
    <div class="loginform">
      <!-- 登录页面提示区域 -->
      <img src="../assets/162632604839859.png" alt="" />
      <div></div>
      <!-- 表单区域 -->
      <div id="formdata">
        <span>账号：</span
        ><el-input
          v-model="username"
          placeholder="请输入账号"
          clearable
        ></el-input
        ><br />
        <span>密码：</span>
        <el-input
          v-model="password"
          placeholder="请输入密码"
          clearable
        ></el-input>
      </div>
      <!-- 登录按钮和清除按钮 -->
      <div class="btnclass">
        <el-button type="primary" @click="adminlogin" :autofocus="true"
          >管理员登录</el-button
        >
        <el-button type="primary" @click="userlogin">企业登录</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    // 点击管理员登录  清除表单
    async adminlogin () {
      const { data: res } = await this.$http.post('admin/login', {
        adminname: this.username.trim(),
        password: this.password.trim()
      })
      if (res.meta.status !== 200) {
        this.username = ''
        this.password = ''
        this.$message.error('登录失败,请重新输入账号密码')
        return ''
      }
      this.$message.success('登录成功')
      // 将用户信息存储到session中
      window.sessionStorage.setItem('username', res.data.admin.adminName)
      // 跳转到其他页面
      this.$router.push('/adminhome')
    },
    // 点击登录按钮的时候今天跳转页面
    async userlogin () {
      alert('这里是用来做企业登录的功能')
      this.$router.push('/home')
    }
  }
}
</script>

<style scoped>
.btnclass {
  position: relative;
  top: 170px;
  left: 100px;
}
.el-button {
  margin: 0 60px;
}
.loginform {
  width: 600px;
  height: 400px;
  margin: 250px auto;
  border: 1px solid #eee;
  box-shadow: 5px 5px 20px;
}
#formdata {
  position: absolute;
  top: 200px;
  width: 300px;
  height: 200px;
  margin: 200px 150px;
}
.el-input {
  width: 240px;
  margin-bottom: 15px;
}
img {
  position: relative;
  left: 70px;
  top: 15px;
}
</style>
